





































































import { Component, Mixins } from 'vue-property-decorator'
import includes from 'lodash/includes'
import PopupMixin from '@/mixins/popup'

@Component
export default class PopupFilterTransaction extends Mixins(PopupMixin) {
  filter: Record<string, any> = {
    currency: '',
    fromDate: '',
    toDate: '',
    fromAmount: '',
    toAmount: '',
    status: ''
  }
  listWallet: Array<Record<string, any>> = [
    {
      id: 5,
      name: 'Lynkey Wallet',
      symbol: 'lynk',
      icon: 'icon-lin'
    },
    {
      id: 0,
      name: 'Bitcoin Wallet',
      symbol: 'btc',
      icon: 'icon-btc'
    },
    {
      id: 1,
      name: 'Ethereum Wallet',
      symbol: 'eth',
      icon: 'icon-eth'
    },
    {
      id: 2,
      name: 'Tether Wallet',
      symbol: 'usdt',
      icon: 'icon-usdt',
      disabled: true
    },
    {
      id: 3,
      name: 'USDC Wallet',
      symbol: 'usdc',
      icon: 'icon-usdc',
      disabled: true
    },
    {
      id: 4,
      name: 'BNB Wallet',
      symbol: 'bnb',
      icon: 'icon-bnb',
      disabled: true
    }
  ]

  listStatus: Array<Record<string, any>> = [
    {
      id: 0,
      label: 'Pending',
      value: 'PENDING'
    },
    {
      id: 1,
      label: 'Processing',
      value: 'PROCESSING'
    },
    {
      id: 2,
      label: 'Success',
      value: 'SUCCESS'
    },
    {
      id: 3,
      label: 'Failed',
      value: 'FAILED'
    },
    {
      id: 4,
      label: 'Rejected',
      value: 'REJECTED'
    }
  ]

  handleReset(): void {
    this.filter = {
      currency: '',
      fromDate: '',
      toDate: '',
      fromAmount: '',
      toAmount: '',
      status: ''
    }
    // this.setOpenPopup({
    //   popupName: 'popup-filter-transaction',
    //   isOpen: false
    // })
    // this.$emit('filter', this.filter)
  }

  handleApply(): void {
    this.setOpenPopup({
      popupName: 'popup-filter-transaction',
      isOpen: false
    })
    let _currency = ''
    if (this.filter.currency) {
      _currency = this.filter.currency.join(',')
    }
    this.$emit('filter', { ...this.filter, currency: _currency })
  }

  onlyNumber(event: KeyboardEvent, type: string): void {
    let keyCode = event.keyCode ? event.keyCode : event.which
    //if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
    // 46 is dot
    if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
      event.preventDefault()
    }
    if (keyCode === 46 && includes(this.filter[type], '.')) {
      event.preventDefault()
    }
  }

  numberFormat(event: FocusEvent): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      fnumber = fnumber.replaceAll(',', '')
      fnumber = this.$options.filters?.numberWithCommas(fnumber)
      _event.target.value = fnumber
    }
  }
}
